 export function isInnerIPFn(){
  //
   var BASE_URL = ''
  // url
  var curPageUrl = window.location.href;
  console.log('curPageUrl-0 '+curPageUrl);

  var reg1 = /(http|ftp|https|www):\/\//g;//
  curPageUrl =curPageUrl.replace(reg1,'');
  console.log('curPageUrl-1 '+curPageUrl);

  var reg2 = /\:+/g;//
  curPageUrl =curPageUrl.replace(reg2,'.');
  console.log('curPageUrl-2 '+curPageUrl);

  curPageUrl = curPageUrl.split('.');//
  console.log('curPageUrl:',curPageUrl);

  var ipAddress = curPageUrl[0]+'.'+curPageUrl[1]+'.'+curPageUrl[2]+'.'+curPageUrl[3];

  var isInnerIp = false;//IPIP
  var ipNum = getIpNum(ipAddress);
  /**
 * IP
  *   A 10.0.0.0 -10.255.255.255
 * B 172.16.0.0 -172.31.255.255
 * C 192.168.0.0 -192.168.255.255
 * D 127.0.0.0 -127.255.255.255()
    **/
  var aBegin = getIpNum("10.0.0.0");
  var aEnd = getIpNum("10.255.255.255");

  var bBegin = getIpNum("172.16.0.0");
  var bEnd = getIpNum("172.31.255.255");

  var cBegin = getIpNum("192.168.0.0");
  var cEnd = getIpNum("192.168.255.255");

  var dBegin = getIpNum("127.0.0.0");
  var dEnd = getIpNum("127.255.255.255");

  isInnerIp = isInner(ipNum,aBegin,aEnd) || isInner(ipNum,bBegin,bEnd) || isInner(ipNum,cBegin,cEnd) || isInner(ipNum,dBegin,dEnd);
  console.log(':'+isInnerIp);
  // return isInnerIp;
  if(isInnerIp){
     BASE_URL = 'http://10.19.8.21:9010' //
  }else{
     BASE_URL = 'http://222.240.233.67:9010' //
  }
  console.log('BASE_URL:',BASE_URL)
  return BASE_URL
}

function getIpNum(ipAddress) {/*IP*/
  var ip = ipAddress.split(".");
  var a = parseInt(ip[0]);
  var b = parseInt(ip[1]);
  var c = parseInt(ip[2]);
  var d = parseInt(ip[3]);
  var ipNum = a * 256 * 256 * 256 + b * 256 * 256 + c * 256 + d;
  return ipNum;
}







function isInner(userIp,begin,end){
  return (userIp>=begin) && (userIp<=end);
}







